<template>
  <div id="sin-outer">
    <h2 v-if="isLoading" class="cat-if">正在加载中...请稍后</h2>
    <ul v-else-if="sinLists.length>0" class="sin-list">
      <li 
        v-for="item in sinLists"
        :key="item.id"
        class="sin-music"
      >
        <img 
          @click="handleSinger(item.id,item.imgUrl,item.sinName)"
          :src="item.imgUrl" 
          alt="歌手" 
          class="sin-img"
        >
        <h4 
          class="sin-songname"
          @click="handleSinger(item.id)"
        >
          {{ item.sinName }}
        </h4>
      </li>
    </ul>
  </div>  
</template>

<script>
import axios from 'axios';

export default {
    name:'SinList',
    data(){
      return {
        sinLists:[],
        sinType:-1,
        sinArea:-1,
        isLoading:true
      }
    },
    created(){
      this.$bus.$on('sendType',(type) => {
        this.sinType = type
        // console.log(type)
      })
      this.$bus.$on('sendArea',(area) => {
        this.sinArea = area
        // console.log(area)
      })
      this.fetchSingers()
    },
    beforeDestroy(){
      this.$bus.$off('sendType')
      this.$bus.$off('sendArea')
    },
    watch:{
      sinType(){
        this.fetchSingers()
      },
      sinArea(){
        this.fetchSingers()        
      }
    },
    methods:{
      async fetchSingers(){
        this.isLoading = true
        try{
          const response = await axios.get('https://apis.netstart.cn/music/artist/list',
            {
              params:{
                type:this.sinType,
                area:this.sinArea,
                initial:'b',
                limit:20
              }
            }
          )
          const rawData = response.data.artists
          // console.log(rawData)
          this.sinLists = rawData.map(item => ({
            sinName:item.name,
            imgUrl:item.picUrl,
            id:item.id
          }))
        }catch(error){
          console.error('请求失败',error)
        }finally{
          this.isLoading = false
        }
      },
      handleSinger(singerId,sinImg,sinName){
        this.$bus.$emit('sendId',singerId)
        this.$emit('isDeeper')
        this.$emit('sendData',sinImg,sinName)
      }
    }
}
</script>

<style scoped>
* {
    margin: 0;
    padding: 0;
  }
  #sin-outer {
    padding: 20px 100px;
  }
  .sin-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 10px;
  }
  .sin-music {
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    /* 主轴垂直轴 */
    align-items: center;
    /* 主轴 */
    justify-content: space-around;
    margin: 10px;
  }
  .sin-img {
    width: 150px;
    height: 150px;
    transition: transform 0.3s ease;
  }
  .sin-img:hover {
    cursor: pointer;
    transform: scale(1.05);
  }
  .sin-songname {
    font-size: 15px;
    font-weight: 500;
    font-family:Arial, Helvetica, sans-serif;
    margin: 5px;
    margin-bottom: 5px;
  }
  .sin-songname:hover {
    color: red;
    cursor: pointer;
  }
</style>