<template>
  <div id="slide-show">
    <!-- 轮播图 -->
    <h1 class="slide-title">热歌推荐</h1>
    <div class="silde-list">
      <img 
        v-for="(img,index) in imgList"
        :key="index"
        :src="img" 
        alt="轮播图" 
        v-show="currentIndex === index"
        class="slide-img"
      >
    </div>
    <!-- 分页器圆点 -->
    <div class="dots">
      <span
        v-for="(img,index) in imgList"
        :key="index"
        @click="handleDotClick(index)"
        :class="{ active:currentIndex === index }"
      >
      </span>
    </div>
    <!-- 左右箭头 -->
    <div class="prev-change change" @click="prev"></div>
    <div class="next-change change" @click="next"></div>
  </div>  
</template>

<script>
export default {
  name:'SlideShow',
  data(){
    return {
      imgList:[
        'https://p5.music.126.net/obj/wonDlsKUwrLClGjCm8Kx/61473954440/9f1a/70ae/438e/ad20dbb67bf8a9a8331586ce4c8ceb3f.jpg',
        'https://p5.music.126.net/obj/wonDlsKUwrLClGjCm8Kx/61481253940/0047/317b/6d2f/ec72afc099328f11c65517a0c24e4bc8.jpg',
        'https://p5.music.126.net/obj/wonDlsKUwrLClGjCm8Kx/61476071540/7359/c010/12d5/59fffe3ba4b283265fbb921a759b6fcc.jpg',
        'https://p5.music.126.net/obj/wonDlsKUwrLClGjCm8Kx/61414315051/320f/2f71/ade1/b6c7c562e0e2b9be398886c8e25156df.jpg',
        'https://p5.music.126.net/obj/wonDlsKUwrLClGjCm8Kx/61474753465/1327/8e52/889a/1a08fb773880c5d659c15cb6ba7ef562.jpg'
      ],
      currentIndex: 0,
      timer: null
    }
  },
  mounted(){
    this.startTimer()
    this.$el.addEventListener('mouseenter',this.stopTimer)
    this.$el.addEventListener('mouseleave',this.startTimer)
  },
  beforeDestroy(){
    this.stopTimer()
  },
  methods:{
    prev(){
      this.currentIndex = (this.currentIndex - 1 + this.imgList.length) % this.imgList.length
    },
    next(){
      this.currentIndex = (this.currentIndex + 1) % this.imgList.length
    },
    handleDotClick(index){
      this.currentIndex = index
    },
    startTimer(){
      this.timer = setInterval(() => {
        this.next()
      },2000)
    },
    stopTimer(){
      clearInterval(this.timer)
    }
  }
}
</script>

<style scoped> 
  * {
    margin: 0;
    padding: 0;
  }
  #slide-show {
    height: 480px;
  }
  .slide-title {
    font-weight: 1000;
    margin-bottom: 30px;
    position: relative;
    left: -5px;
  }
  .silde-list {
    text-align: center;
  }
  .slide-img {
    width: 850px;
    height: 350px;
    transition: transform 0.3s ease;
  }
  .slide-img:hover {
    cursor: pointer;
    transform: scale(1.03);
  }
  .dots span {
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background-color: rgb(185, 181, 181);
    margin: 0 10px;
  }
  .dots span:hover {
    background-color: gray;
    cursor: pointer;
  }
  span.active {
    background-color: gray;
  }
  .change {
    width: 0;
    height: 0;
    transform: translateY(-50%);
    border-top: 40px solid transparent;
    border-bottom: 40px solid transparent;
  }
  .change:hover {
    cursor: pointer;
  }
  .prev-change {
    position: relative;
    top: -210px ;
    left: 100px;
    border-right: 40px solid rgb(182, 179, 179);
  }
  .prev-change:hover {
    border-right-color: gray;
  }
  .next-change {
    position: relative;
    top: -280px;
    left: 1090px;
    border-left: 40px solid rgb(182, 179, 179);
  }
  .next-change:hover {
    border-left-color: gray;
  }
</style>