import Vue from "vue";
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        collectedMusic:[]
    },
    mutations:{
        // 是否已收藏
        addCollect(state,song){
            const isExist = state.collectedMusic.some(item => item.songId === song.songId)
            if(!isExist){
                state.collectedMusic.push(song)
            }
        },
        // 取消收藏
        removeCollect(state,songId){
            state.collectedMusic = state.collectedMusic.filter(item => item.songId !== songId)
        }
    }
})