import VueRouter from "vue-router";
//一级路由
import RecommendMusic from '../pages/RecommendMusic.vue'
import MusicCategories from '../pages/MusicCategories.vue'
import TheSingers from '../pages/TheSingers.vue'
import MyLove from '../pages/MyLove.vue'

export default new VueRouter({
    routes:[
        {
            //一进来就是
            path:'/',
            redirect:'/recommendMusic',
            meta:{
                title:''
            }
        },
        {
            path:'/recommendMusic',
            component:RecommendMusic,
            meta:{
                title:'个性推荐'
            }
        },
        {
            path:'/musicCategories',
            component:MusicCategories,
            meta:{
                title:'歌单分类'
            }
        },
        {
            path:'/theSingers',
            component:TheSingers,
            meta:{
                title:'歌手分类'
            }
        },
        {
            path:'/myLove',
            component:MyLove,
            meta:{
                title:'我的喜欢'
            }
        }
    ]
})