<template>
  <div id="music-list">
    <div class="list-header">
      <h1>我的喜欢</h1>
      <p>共 {{ collectedMusic.length }} 首歌曲</p>
    </div>

    <!-- 空状态 -->
    <div v-if="collectedMusic.length === 0" class="empty">
      暂无喜欢的歌曲
    </div>

    <!-- 歌曲列表 -->
    <ul v-else>
      <li 
        v-for="song in collectedMusic"
        :key="song.songId"
        class="song-item"
      >
        <div class="song-info">
          <div class="song-name">{{ song.name }}</div>
          <div class="singer">{{ song.singer }}</div>
        </div>
        <button 
          @click="handleLike(song)"
          class="like-btn"
        >
          ❤
        </button>
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'

export default {
    name:'LoveList',
    computed:{
      ...mapState(['collectedMusic'])
    },
    methods:{
      ...mapMutations(['addCollect','removeCollect']),
      handleLike(song){
        if(confirm('你确定要取消喜欢这首歌吗？')){
          // this.collectedMusic = this.collectedMusic.filter(item => item.songId !== song.songId)
          this.removeCollect(song.songId)      
        }
      }

    }
}
</script>

<style scoped>
  #music-list {
    width: 900px;
    margin: 20px auto;
    padding: 0 20px;
  }
  .list-header {
    margin-bottom: 10px;
    padding-bottom: 20px;
    border-bottom: 1px solid #eee;
  }
  .list-header h1 {
    margin: 0 0 3px 0;
    font-size: 20px;
  }
  .list-header p {
    margin: 0;
    color: #666;
    font-size: 14px;
  }
  .empty {
    text-align: center;
    padding: 40px 0;
    color: #999;
  }
  .song-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
    border-bottom: 1px solid #eee;
  }
  .song-info .song-name {
    font-size: 16px;
    margin-bottom: 2px;
  }
  .song-name:hover {
    color: red;
    cursor: pointer;
  }
  .song-info .singer {
    font-size: 14px;
    color: #888;
  }
  .like-btn {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    color: #ff3b30;
  }
</style>