<template>
  <div class="singer-card">
    <!-- 歌手信息区域 -->
    <div class="singer-info">
      <!-- 歌手图片（左方） -->
      <img 
        :src="singer.avatar" 
        :alt="singer.name" 
        class="singer-avatar"
      >
      <!-- 歌手名字和信息（右方） -->
      <div class="singer-details">
        <h3 class="singer-name">{{ sinName }}</h3>
        <p class="singer-desc">{{ singer.description }}</p>
      </div>
    </div>
    
    <!-- 歌曲列表区域（下方） -->
    <div class="song-list">
      <div 
        class="song-item" 
        v-for="(song, index) in singer.songs" 
        :key="index"
      >
        <!-- 歌曲名字和作者 -->
        <div class="song-info-left">
          <p class="song-name">{{ song.name }}</p>
          <p class="song-author">{{ authorName }}</p>
        </div>
        <!-- 收藏按钮（红心） -->
        <!-- :class="{ active: song.isFavorite, }" -->
        <div 
          class="favorite-btn" 
          :class="{ collected:isCollected(song.songId)}"
          @click="toggleFavorite(song,index)"
        >
          ❤
        </div>
      </div>
    </div>

    <!-- 返回 -->
    <div>
      <button 
        class="to-back"
        @click="Back"        
      >返回歌手分类页</button>
    </div>
  </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'

export default {
  name: 'SingleSinger',
  // props 定义：规定父组件需要传递的数据格式
  props: {
    sinName:{
      type:String,
      required:true
    },
    authorName:{
      type:String,
      required:true
    },
    // 核心数据：歌手信息对象
    singer: {
      type: Object, // 数据类型必须是对象
      required: true, // 父组件必须传递这个数据（否则会警告）
      // 默认值：当父组件没传时使用（这里因为 required: true，默认值实际很少触发，仅作兜底）
      default: () => ({
        avatar: '', // 歌手头像地址（默认空字符串）
        name: '未知歌手', // 歌手名字（默认提示文字）
        description: '暂无介绍', // 歌手信息（默认提示文字）
        songs: [], // 歌曲列表（默认空数组）
      }),
    },
    onToggleFavorite:{
        type:Function,
        require:true
    }   
  },
  computed:{
    ...mapState(['collectedMusic'])
  },
  methods: {
    // 切换歌曲收藏状态
    toggleFavorite(song,index) {
      this.onToggleFavorite(index)
      console.log(song)
      if(this.isCollected(song.songId)){
        this.removeCollect(song.songId)
      }else{
        this.addCollect(song)
      }
    },
    Back(){
      this.$emit('isBack')
    },
    ...mapMutations(['addCollect','removeCollect']),
    isCollected(songId){
      return this.collectedMusic.some(item => item.songId === songId)
    },
  }
}
</script>

<style scoped>
  .to-back {
    display: inline-block;
    width: 100px;
    height: 30px;
  }
  .to-back:hover {
    cursor: pointer;
  }
.singer-card {
  width: 100%;
  max-width: 600px;
  margin: 15px auto;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  background: #fff;
}

/* 歌手信息样式 */
.singer-info {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.singer-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
  border: 2px solid #f0f0f0;
}

.singer-name {
  margin: 0 0 8px 0;
  font-size: 20px;
  color: #333;
}

.singer-desc {
  margin: 0;
  font-size: 14px;
  color: #666;
  line-height: 1.6;
}

/* 歌曲列表样式 */
.song-list {
  border-top: 1px solid #f0f0f0;
  padding-top: 10px;
}

.song-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid #f5f5f5;
}

.song-item:last-child {
  border-bottom: none;
}

.song-info-left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 15px;
  overflow: hidden;
}

.song-name {
  margin: 0 0 4px 0;
  font-size: 16px;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.song-author {
  margin: 0;
  font-size: 12px;
  color: #999;
}

/* 收藏按钮样式 */
.favorite-btn {
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: #ccc;
  font-size: 18px;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* 激活状态（已收藏） */
.favorite-btn.collected {
  color:  #ff3b30; 
}
</style>