<template>
  <div id="cat-outer">
    <h2 v-if="isLoading" class="cat-if">正在加载中...请稍后</h2>
    <ul v-else-if="catLists.length > 0" class="cat-list">
      <li 
        v-for="(item,index) in catLists"
        :key="index"
        class="cat-music"
      >
        <img :src="item.imgUrl" alt="精品歌单" class="cat-img">
        <h4 class="cat-songname">{{ item.songName }}</h4>
        <span class="cat-nickname">{{ item.autorName }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios'

export default {
    name:'CatList',
    data(){
      return {
        catLists:[],
        cat:'全部歌单',
        isLoading: true
      }
    },
    mounted(){
      this.$bus.$on('sendCat',(cat) => {
        this.cat = cat
      })
      this.fetchCatLists()
    },
    beforeDestroy(){
      this.$bus.$off('sendCat')
    },
    methods:{
      async fetchCatLists(){
        this.isLoading = true
        try {
          const response = await axios.get('https://apis.netstart.cn/music/top/playlist',
          {
            params:{
              limit:20,
              order:'hot',
              cat:this.cat
            }
          })
          // console.log(response)
          const rawData = response.data.playlists
          this.catLists = rawData.map(item => ({
            imgUrl: item.coverImgUrl,
            songName: item.name,
            autorName: item.creator.nickname
          }))
          // console.log(this.playLists)
        }catch(error){
          console.error('请求失败',error)
        }finally{
          this.isLoading = false
        }
      }
    },
    watch:{
      cat(){
        this.fetchCatLists()
      }
    }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
  #cat-outer {
    padding: 20px 100px;
  }
  .cat-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 10px;
  }
  .cat-music {
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    /* 主轴垂直轴 */
    align-items: center;
    /* 主轴 */
    justify-content: space-around;
    margin: 20px;
  }
  .cat-img {
    width: 150px;
    height: 150px;
    transition: transform 0.3s ease;
  }
  .cat-img:hover {
    cursor: pointer;
    transform: scale(1.05);
  }
  .cat-songname {
    font-size: 15px;
    font-weight: 500;
    font-family:Arial, Helvetica, sans-serif;
    margin: 10px;
    margin-bottom: 5px;
  }
  .cat-nickname {
    font-size: 14px;
    display: inline-block;
    color: grey;
  }
  .cat-songname:hover,
  .cat-nickname:hover {
    color: red;
    cursor: pointer;
  }
</style>