<template>
  <div id="outer">
    <SingleSinger 
      v-if="isSingleSinger"
      :singer="currentSinger"
      :onToggleFavorite="handleToggleFavorite"
      @isBack="isBack"
      :sinName="sinName"
      :authorName="authorName"
    />
    <div id="inner" v-else>
      <SingerList/>
      <SinList
        @isDeeper="isDeeper"
        @sendData="receiveData"
      />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import SinList from '../components/SinList.vue'
import SingerList from '../components/SingerList.vue'
import SingleSinger from '@/components/SingleSinger.vue'

export default {
    name:'TheSingers',
    components:{
        SinList,
        SingerList,
        SingleSinger
    },
    data(){
      return {
        sinName:'',
        authorName:'',
        singerId:null,
        isSingleSinger:false,
        currentSinger:{
          avatar:'http://example.com/zhou.jpg',
          name:'周杰伦',
          description:'流行音乐歌手',
          songs:[
            // {name:'晴天',author:'周杰伦',isFavorate:false},
            // {name:'七里香',author:'周杰伦',isFavorate:false},
            // {name:'青花瓷',author:'周杰伦',isFavorate:true}
          ]
        }
      }
    },
    mounted(){
      this.$bus.$on('sendId',(singerId)=>{
        this.singerId = singerId
      })
    },      
    beforeDestroy(){
      this.$bus.$off('sendId')
    },
    methods:{
      handleToggleFavorite(index){
        const newSongs = [...this.currentSinger.songs]
        newSongs[index].isFavorate = !newSongs[index].isFavorate
        this.currentSinger = {...this.currentSinger,songs:newSongs}
      },
      async ToSingleSinger(newId){
        try{
          const response = await axios.get('https://apis.netstart.cn/music/artist/top/song',({
            params:{
              id:Number(newId)
              // id:36181946
            }
          }))
          const rawData = response.data.songs
          console.log(rawData)
          this.currentSinger.songs = rawData.map(song => ({
            name:song.name,
            isFavorate:false,
            songId:song.no
          }))
        }catch(error){
          console.error('请求失败',error)
        }
      },
      isDeeper(){
        this.isSingleSinger = true
      },
      isBack(){
        this.isSingleSinger = false
      },
      receiveData(sinImg,sinName){
        this.sinName = sinName
        this.currentSinger.avatar = sinImg
        console.log(sinImg,sinName)
        this.authorName = sinName
      }
    },
    watch:{
      singerId(newId){
        this.ToSingleSinger(newId)
      }
    }
}
</script>

