<template>
  <div id="singer-outer">
    <h1 class="singer-h1">歌手分类</h1>
    <div class="singer singer-first">
      <span class="singer-span">歌手:</span>
      <ul class="singer-ul">
        <li 
          v-for="(singerItem,index) in singerLists"
          :key="index"
          @click="fetchOne(singerItem.type,index)"
          :class="{ active: currentActiveOne === index}"
          class="singer-li"
        >
          <a href="javascript:;" class="singer-a">{{ singerItem.singerCat }}</a>
        </li>
      </ul>
    </div>

    <div class="singer">
      <span class="singer-span">地区:</span>
      <ul class="singer-ul">
        <li 
          v-for="(areaItem,index) in areaLists"
          :key="index"
          @click="fetchTwo(areaItem.area,index)"
          :class="{ active: currentActiveTwo === index}"
          class="singer-li"
        >
          <a href="javascript:;" class="singer-a">{{ areaItem.areaCat }}</a>
        </li>
      </ul>
    </div>
  </div>  
</template>

<script>
export default {
    name:'SingerList',
    data(){
      return {
        singerLists:[
          {type:-1,singerCat:'全部'},
          {type:1,singerCat:'男歌手'},
          {type:2,singerCat:'女歌手'},
          {type:3,singerCat:'乐队'}
        ],
        areaLists:[
          {area:-1,areaCat:'全部'},
          {area:7,areaCat:'华语'},
          {area:8,areaCat:'日本'},
          {area:16,areaCat:'韩国'},
          {area:96,areaCat:'欧美'},
          {area:0,areaCat:'其他'}
        ],
        currentActiveOne:0,
        currentActiveTwo:0
      }
    },
    methods:{
      fetchOne(type,index){
        this.currentActiveOne = index
        this.$bus.$emit('sendType',type)
      },
      fetchTwo(area,index){
        this.currentActiveTwo = index
        this.$bus.$emit('sendArea',area)
      }
    }
}
</script>

<style scoped>
  #singer-outer {
    margin: 20px auto;
  }
  .singer-ul {
    display: flex;
    flex-wrap: wrap;
  }
  .singer-li {
    list-style: none;
    margin: 10px;
  }
  .singer-a {
    display: inline-block;
    text-decoration: none;
    color: black;
    font-size: 18px;
    margin: 10px;
    font-weight: 200;
  }
  .singer-a:hover {
    color: red;
  }
  .singer {
    display: flex;
    justify-content: center;
  }
  .singer-span {
    display: inline-block;
    margin: 10px 0;
    text-align: center;
    justify-content: center;
    align-items: center;
    padding: 10px;
    font-size: 18px;
  }
  .singer-first {
    padding-top: 30px;
  }
  li.active a {
    color: red;
  } 
</style>