<template>
  <div id="cat-all">
    <h1 class="cat-title">歌单分类：</h1>
    <div class="cat-music">
      <h2 class="ul-title">歌曲风格：</h2>
      <ul class="cat-ul">
        <li 
          @click="expressAndGetAll"
          :class="{ active: currentActive === -1}"
          class="all-music cat-li" 
        >
          <a href="javascript:;" class="cat-a">全部歌单</a>
        </li>
        <li 
          v-for="(item,index) in catList"
          :key="index"
          :class="{ active: currentActive === index}"
          @click="expressAndGet(index,item.cat)"
          class="cat-li"
        >
          <a href="javascript:;" class="cat-a">{{ item.cat }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
    name:'CategoriesList',
    data(){
      return {
        catList:[],
        currentActive:-1,
        category:'全部歌单'
      }
    },
    created(){
      this.fetchCatLists()
    },
    methods:{
      async fetchCatLists(){
        try{
          const response = await axios.get('https://apis.netstart.cn/music/playlist/catlist')
          const rawData = response.data.sub.slice(0,30)
          console.log(rawData)
          this.catList = rawData.map(item => ({
            cat: item.name
          }))
          // console.log(this.catList)
        }catch(error){
          console.error('请求失败',error)
        }
      },
      expressAndGetAll(){
        this.currentActive = -1
        this.category = '全部歌单'
        // console.log(this.category)
        this.$bus.$emit('sendCat',this.category)
      },
      expressAndGet(index,cat){
        this.currentActive = index
        this.category = cat
        // console.log(this.category)
        this.$bus.$emit('sendCat',this.category)
      }
    }
}
</script>

<style scoped>
  .cat-ul {
    display: flex;
    flex-wrap: wrap;
  }
  .cat-li {
    margin: 10px;
    list-style: none;
  }
  .cat-a {
    text-decoration: none;
    color: black;
    font-size: 17px;
  }
  .cat-a:hover {
    cursor: pointer;
    color: red;
  }
  #cat-all {
    width: 1000px;
    margin: 20px auto;
  }
  .cat-music {
    display: flex;
    flex-wrap: wrap;
  }
  .cat-music h2 {
    margin: 10px;
  }
  li.active a {
    color: red;
  }
</style>