<template>
  <div id="rec-outer">
    <h1 class="rec-title">
      精选歌单
    </h1>
    <ul class="rec-list">
      <li 
        v-for="(item,index) in playLists"
        :key="index"
        class="rec-music"
      >
        <img :src="item.imgUrl" alt="精品歌单" class="rec-img">
        <h4 class="rec-songname">{{ item.songName }}</h4>
        <span class="rec-nickname">{{ item.autorName }}</span>
      </li>
    </ul>
  </div>  
</template>

<script>
import axios from 'axios'

export default {
    name:'ReList',
    data(){
      return {
        playLists:[]
      }
    },
    created(){
      this.fetchPlayLists()
    },
    methods:{
      async fetchPlayLists(){
        try {
          const response = await axios.get('https://apis.netstart.cn/music/top/playlist',
          {
            params:{
              limit:20,
              order:'hot'
            }
          })
          // console.log(response)
          const rawData = response.data.playlists
          this.playLists = rawData.map(item => ({
            imgUrl: item.coverImgUrl,
            songName: item.name,
            autorName: item.creator.nickname
          }))
          // console.log(this.playLists)
        }catch(error){
          console.error('请求失败',error)
        }
      }
    }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
  #rec-outer {
    padding: 0 100px;
  }
  .rec-title {
    font-weight: 1000;
    margin-bottom: 30px;
  }
  .rec-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 10px;
  }
  .rec-music {
    width: 200px;
    height: 200px;
    display: flex;
    flex-direction: column;
    /* 主轴垂直轴 */
    align-items: center;
    /* 主轴 */
    justify-content: space-around;
    margin: 15px;
  }
  .rec-img {
    width: 150px;
    height: 150px;
    transition: transform 0.3s ease;
  }
  .rec-img:hover {
    cursor: pointer;
    transform: scale(1.05);
  }
  .rec-songname {
    font-size: 15px;
    font-weight: 500;
    font-family:Arial, Helvetica, sans-serif;
    margin: 10px;
    margin-bottom: 5px;
  }
  .rec-nickname {
    font-size: 14px;
    display: inline-block;
    color: grey;
  }
  .rec-songname:hover,
  .rec-nickname:hover {
    color: red;
    cursor: pointer;
  }
</style>